import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'fabricante-dados',   templateUrl: 'fabricante_dados.component.html' }) export class FabricanteDadosComponent implements OnInit {       displayMode: FabricanteDadosDisplayModeEnum;     displayModeEnum = FabricanteDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = FabricanteDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = FabricanteDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = FabricanteDadosDisplayModeEnum.Edit;           break;       }     }  }  enum FabricanteDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


