import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Fabricante } from '../../../modelo/Fabricante'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'fabricante-dados-details',   templateUrl: 'fabricante_dadosDetails.component.html' }) export class FabricanteDadosDetailsComponent implements OnInit {    fabricante: Fabricante;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.fabricante.Fabricante";                 let id = +params['id'];         this.fabricante = new Fabricante();         this.fabricante.cd_fabricante = id;         o.objetoJson = this.fabricante;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a fabricante. '; }      this.fabricante = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


